@import url('https://fonts.googleapis.com/css2?family=Courier+Prime&family=Roboto+Mono&display=swap');

html,
body {
    color: #37352a;
    background: #f7f7f2;
    font-style: normal;
}

body a {
    text-decoration: none;
    color: #f7f7f2;
    background-color: #775e42;
    border-radius: 5px;
    padding: 3px 7px;
}

p {
    padding-top: 5px;
    line-height: 25px;
}

body li {
    margin: 10px;
}

html {
    height: 100%;
}

body {
    font-family: "Courier Prime", monospace;
    // font-size: 90%;
    line-height: 1.2;
    margin: 0;
    min-height: 100%;
    overflow-wrap: break-word;
    text-align: justify;
}

.webring {
    display: flex;
    justify-content: space-between;
    width: 50%;
}

header h1 {
    text-align: center;
    display: flex;
    font-size: 2em;
}


h3,
h4,
h5,
h6 {
    margin-top: 1.2em;
}

p {
    margin: 10px;
}

li {
    margin: 0.4rem 0;
}

*:target {
    background: yellow;
}

.w {
    max-width: 800px;
    margin: 0 auto;
    padding: 50px;
}

hr {
    margin: 1.4rem 0;
    text-align: center;
    border: 0;

    &:before {
        content: "---";
    }

    &:after {
        content: attr(data-content) "---";
    }
}

h2 {
    margin-bottom: 0px;
    margin-top: 2.5em;
}

.dt {
    color: #aaa;
    padding: 0px 0px 10px 0px;
}

table {
    width: 100%;
}

table,
th,
td {
    border: thin solid black;
    border-collapse: collapse;
    padding: 0.4rem;
}

pre {
    margin: 10px;
    padding: 10px 15px;
    border-radius: 5px;
    border: 2px solid black;
    overflow-x: auto;
}

code {
    font-family: 'Roboto Mono', monospace;
    font-size: 90%;
}

code.has-jax {
    -webkit-font-smoothing: antialiased;
    background: inherit !important;
    border: none !important;
    font-size: 100%;
}

blockquote {
    border: solid #444;
    padding: 2rem;
    margin: 10px 0;
    border-radius: 5px;

    p {
        margin: 0;
    }
}

img {
    max-width: 100%;
    align-content: center;
    border-radius: 5px;
    margin: 0 auto;
}

figcaption {
    text-align: center;
    opacity: 0.5;
}

.dsclmr {
    padding: 10px;
    background-color: #b93a3d;
    color: #f7f7f2;
    border-radius: 5px;
    text-align: center;
}

.house {
    font-size: 2em;
    display: flex;
    background-color: #f7f7f2;
}
